import React from 'react';
import { FaBookOpen, FaChalkboardTeacher, FaLaptopCode, FaHandsHelping } from 'react-icons/fa';

const KeyFeaturesSection = () => {
  return (
    <div id="cat_key_features" className="cat_key_features_even bg-blue-500 py-16 text-center">
      <div className="container mx-auto">
        <h2 className="course_heading text-white text-2xl lg:text-3xl font-bold mb-6">
          Key Features of AWS Training at QBee
        </h2>
        <div className="key_features_even">
          <ul className="list-none text-white text-left">
            <li className="flex items-center justify-center mb-4">
              <span className="inline-block mr-3 text-2xl text-green-500">
                <FaBookOpen />
              </span>
              Access to Official AWS Curriculum
            </li>
            <li className="flex items-center justify-center mb-4">
              <span className="inline-block mr-3 text-2xl text-green-500">
                <FaChalkboardTeacher />
              </span>
              Training by Industry Certified AWS Trainers
            </li>
            <li className="flex items-center justify-center mb-4">
              <span className="inline-block mr-3 text-2xl text-green-500">
                <FaLaptopCode />
              </span>
              Option for Instructor-led Live and Virtual Sessions
            </li>
            <li className="flex items-center justify-center">
              <span className="inline-block mr-3 text-2xl text-green-500">
                <FaHandsHelping />
              </span>
              Post-training support: mock exams, mentoring, resume preparation, interview opportunities, and more
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default KeyFeaturesSection;
