import React from 'react'
import BannerSection from './BannerSection'
import FeatureSection from './FeatureSection'
import HackathonEventSection from './HackathonEventSection'
import ExpertiseSection from './ExpertiseSection'
import KeyFeaturesSection from './KeyFeaturesSection'
import AdvantageSection from './AdvantageSection'
import CourseCarousel from './CourseCarousel'
import AzureMasteryPass from './AzureMasteryPass'
import FAQSection from './FAQSection'
import WhyCloudThat from './WhyCloudThat'

const Aws = () => {
  return (
    <div className="">
        <BannerSection />
              
      <FeatureSection />
    <div className="container mx-auto px-8 sm:px-8 lg:px-10">

      <HackathonEventSection />
      <ExpertiseSection />
      <KeyFeaturesSection />
      <AdvantageSection />
      <CourseCarousel />
      <AzureMasteryPass />
      <FAQSection />
      <WhyCloudThat />
    </div>
    </div>
  )
}

export default Aws
