import React from 'react';

const BannerSection = () => {
  return (
    <section
      className="relative bg-cover bg-center mt-20 py-16 lg:h-[90vh]" 
      style={{ backgroundImage: 'url(https://img.freepik.com/premium-photo/man-touching-connect-data-information-cloud-computing-technology-internet-storage-network-concept-large-database-big-data-through-internet-technology-cloud-sharing-download-upload_162459-2444.jpg?ga=GA1.1.2071195669.1721226201&semt=sph)' }}
    >
      <div className="container mx-auto px-4 h-full flex items-center">
        <div className="bnr_content_img col-lg-8 col-md-7 lg:ml-16">
          <div className="cc_banner_content">
            <div className="cc_banner_title">
              <h1 className="text-3xl lg:text-4xl font-bold text-white">
                QBee: Leading AWS Training <br></br>in Chennai
              </h1>
              <p className="cc_banr_desc mt-4 text-white">
                AWS Training in Chennai by CloudThat offers comprehensive training with <br></br>
                industry-specific courseware, hands-on labs, and certification-focused mock exams <br></br>
                to create industry-ready professionals.
              </p>
              <a
                href=""
                className="cc_btn cc_btn_action_21 mt-6 inline-block bg-blue-500 text-white py-2 px-4 rounded-lg"
                data-toggle="modal"
                data-target="#explore_course"
              >
                Request a Call Back
              </a>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default BannerSection;
